@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@400..900&family=Press+Start+2P&display=swap");

:root {
	--bg: #2a2828;
	--primary: hsl(0, 62%, 32%);
	--secondary: hsl(204, 92%, 38%);
	--highlight: #bfb263;
	--text: #dcdada;
	--bg-gradient: repeating-linear-gradient(
			to left,
			rgb(238 228 228 / 13%) 0px,
			rgb(255 255 255 / 10%) 1px,
			transparent 1px,
			transparent 15px
		),
		linear-gradient(180deg, hsl(200 42% 16% / 1) 0%, hsl(204 92% 3% / 1) 100%);
	--bit-font: "Press Start 2P", system-ui;
	--orbit-font: "Orbitron", sans-serif;
}
.bit-font {
	font-family: "Press Start 2P", system-ui;
	font-weight: 400;
	font-style: normal;
}

.orbit-font {
	font-family: "Orbitron", sans-serif;
	font-optical-sizing: auto;
	font-weight: 400;
	font-style: normal;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
a {
	text-decoration: none;
	columns: rgb(145, 73, 73);
}
body {
	margin: 0;
	font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
		Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

.nav-bar {
	display: flex;
	align-items: center;
	padding-block: 0.5rem;
	justify-content: space-around;
	width: 87%;
	height: fit-content;
	background-color: hsl(204, 92%, 8%);
	position: absolute;
	bottom: 10px;
	left: 50%;
	transform: translate(-50%, -50%);
	border-radius: 10px;
	font-size: 13px;
}
.nav-bar a:hover {
	transform: scale(1.15);
}
.docker-links {
	width: 30px;
	height: 30px;
}
.nav-logo {
	width: 50px;
	animation: App-logo-spin infinite 20s linear;
}
.box {
	position: absolute;
	background-color: transparent;
	border: none;
	width: 90px;
	height: auto;
	max-width: 20 0px;
	margin: 2rem;
}
.top-dock {
	display: flex;
	width: 100%;
	color: var(--text);
	background-color: var(--secondary)
}
#top-dock-logo {
	margin-inline: 0.5rem;
	fill: white;
	display: flex;
	align-items: center;
}
.top-dock-text {
	font-weight: 300;
	font-size: .75rem;
	font-family: var(--orbit-font);
}
.top-dock-right {
	display: flex;
	align-items: center;
	margin-inline: 1rem;
	font-weight: 500;
	color: whitesmoke;
}
.top-dock-left {
	font-size: 10px;
	color: whitesmoke;
	margin-inline: 1rem;
}

.splash-container {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100vh;
	background: var(--bg-gradient);
	color: white;
	font-size: 2rem;
	text-align: center;
	z-index: 100;
}

.splash-logo {
	animation: Grow infinite 7s linear;
	z-index: 102;
}
.splash-text {
	font-size: 2rem;
}
.atom-logo {
	clip-path: circle(50% at 50% 47%);
}
.center {
	display: flex;
	justify-content: center;
	align-items: center;
}
@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}
@media screen and (max-width: 600px) {
	.App {
		background-position: 20% 30%;
	}
	.nav-bar a img {
		width: 24px;
		height: 30px;
	}
	.box {
		width: 60px;
	}
	.macos-text {
		font-size: 10px;
		white-space: nowrap;
		text-align: center;
		margin-block: auto;
		margin-right: -2px;
		width: 100%;
		display: none;
	}

	#container {
		width: 85%;
	}
	.top-dock-date {
		display: none;
	}
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
		/* transform: rotateX(150deg); */
	}
}

@keyframes Grow {
	from {
		transform: scale(1);
	}
	to {
		transform: scale(1.3);
		/* transform: rotateX(150deg); */
	}
}
